import React, { FC } from 'react'
import { useForm } from 'react-hook-form'
import { graphql } from 'gatsby'
import { GatsbyImage } from 'gatsby-plugin-image'
import Layout from 'components/Layout'
import { Grid, Title, Text, Button, Input, FormSelect } from 'components/theme'
import { Box } from 'styles/globalStyle'
import { AppointmentPageTypes } from 'types/appointmentPageTypes'
import Checkbox from 'components/theme/Checkbox'

type AppointmentPageParams = {
  data: AppointmentPageTypes
}

const AppointmentPage: FC<AppointmentPageParams> = ({ data }) => {
  const { title, subtitle, image } = data.prismicAppointmentPage.data
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    defaultValues: {
      lastName: '',
      firstName: '',
      email: '',
      phoneNumber: '',
      center: '',
      conditions: false,
    },
  })
  const submitForm = (data: any) => console.log(data)

  return (
    <Layout onlyFooter>
      <Grid columns={['1fr', '', '', 'repeat(2, 1fr)']}>
        <Box display={['none', '', '', 'block']}>
          <GatsbyImage
            alt={image.alt}
            image={image.gatsbyImageData}
            style={{
              height: '100%',
              width: '100%',
              minHeight: '100vh',
            }}
            imgStyle={{
              objectPosition: 'top',
            }}
          />
        </Box>
        <Box padding={['2rem 1rem', '', '', '4rem 2rem', '', '6rem 6.5rem']}>
          <Title size="sm" mb="1.25rem">
            {title.text}
          </Title>
          <Text size="xl" mb="3rem">
            {subtitle.text}
          </Text>
          <form onSubmit={handleSubmit(submitForm)}>
            <Input
              label="Nom"
              register={register}
              value="lastName"
              placeholder="Entrer votre nom"
              errors={errors}
              mb="1.5rem"
              required
            />
            <Input
              label="Prénom"
              register={register}
              value="firstName"
              placeholder="Entrer votre prénom"
              errors={errors}
              mb="1.5rem"
              required
            />
            <Input
              type="email"
              label="Email"
              register={register}
              value="email"
              placeholder="Entrer votre adresse email"
              errors={errors}
              mb="1.5rem"
              required
            />
            <Input
              type="tel"
              label="Téléphone"
              register={register}
              value="phoneNumber"
              placeholder="Entrer votre numéro de téléphone"
              errors={errors}
              mb="1.5rem"
              required
            />
            <FormSelect
              label="Votre centre auditif"
              value="center"
              register={register}
              errors={errors}
              placeholder="Sélectionner votre centre auditif"
              options={data.centers.nodes.map((center) => ({
                value: center.uid,
                label: center.data.title.text,
              }))}
              required
            />
            <Checkbox
              label="J'accepte la politique de protection des données personnelles"
              register={register}
              errors={errors}
              value="conditions"
              mt="1.5rem"
              required
            />
            <Button type="gradient" mt="2rem" size="xl" width="100%">
              Rappelez-moi
            </Button>
          </form>
        </Box>
      </Grid>
    </Layout>
  )
}

export const query = graphql`
  query {
    prismicAppointmentPage {
      data {
        title {
          text
        }
        subtitle {
          text
        }
        image {
          gatsbyImageData
          alt
        }
      }
    }
    centers: allPrismicCenters {
      nodes {
        data {
          title {
            text
          }
        }
        uid
      }
    }
  }
`

export default AppointmentPage
